//
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
//
import Images from '../../Component/Images/image';
//
import { useEffect, useState } from 'react';
//
import restfulApi from '../../restfulApi';
//
import { useNavigate } from 'react-router-dom';
//
import Tippy from '@tippyjs/react';
import 'tippy.js/dist/tippy.css'; // optional
import 'tippy.js/themes/light.css';
//
import classNames from 'classnames/bind';
import style from './ShoppingCart.module.scss';
const cx = classNames.bind(style);
function ShoppingCart() {
    const navigator = useNavigate();
    //
    const [showClassification, setShowClassification] = useState({});
    const [showAction, setShowAction] = useState({});

    // size
    const [size, setSize] = useState('');

    // data from DB
    const [data, setData] = useState([]);

    const [selectedProducts, setSelectedProducts] = useState({}); // Lu trng thi ca tng checkbox

    const [countProductDB, setCountProductDB] = useState(0);
    const [totalPriceDB, setTotalPriceDB] = useState(0);

    // local
    // data shopping cart in localSto
    const [dataShoppingCartLocal, setDataShoppingCartLocal] = useState([]);

    const [selectedProductsLocal, setSelectedProductsLocal] = useState({}); // Lu trng thi ca tng checkbox Local

    // x l m sn phm  check
    const [countProduct, setCountProduct] = useState(0);
    // tnh gi tin
    const [countPrice, setCountPrice] = useState(0);

    // handle hide voucher
    const [showVoucher, setShowVoucher] = useState(false);

    // payUrl
    const [payUrl, setPayUrl] = useState('');
    const [showPayCK, setShowPayCK] = useState(false);

    // payment
    const [dataPayment, setDataPayment] = useState([]);

    // check login
    const getCookie = (name) => {
        const value = `; ${document.cookie}`;
        const parts = value.split(`; ${name}=`);
        if (parts.length === 2) {
            return decodeURIComponent(parts.pop().split(';').shift());
        }
        return null;
    };
    const checkLogin = getCookie('status');

    // code Database
    // take data from DB
    useEffect(() => {
        const takeShoppingCart = async () => {
            let res = await restfulApi.takeDataShoppingCart();
            if (res && res.data && res.data.EC === 0) {
                setData(res.data.DT);
            }
        };
        takeShoppingCart();
    }, []);

    // Hm x l khi thay i trng thi checkbox ca tng sn phm
    const handleCheckboxChange = (id) => {
        setSelectedProducts((prevSelectedProducts) => ({
            ...prevSelectedProducts,
            [id]: !prevSelectedProducts[id], // o trng thi checkbox
        }));
    };

    // x l show Classification
    const handleClassification = (id) => {
        setShowClassification((prev) => ({
            ...prev,
            [id]: !prev[id],
        }));
    };

    // x l setShowAction
    const handleAction = (id) => {
        setShowAction((prev) => ({
            ...prev,
            [id]: !prev[id],
        }));
    };

    // x l turn db
    const handleTurnPrevDB = (id) => {
        const updataTurnDB = data.map((item) =>
            item.productId === id && item.amount > 1 ? { ...item, amount: item.amount - 1 } : item,
        );
        let updataTurnAndPrice = updataTurnDB.map((item) =>
            item.productId === id
                ? {
                      ...item,
                      priceT: item.amount * parseInt(item.price.replace(/\./g, ''), 10),
                  }
                : item,
        );
        setData(updataTurnAndPrice);
    };

    const handleTurnNextDB = (id) => {
        const updataTurnDB = data.map((item) => (item.productId === id ? { ...item, amount: item.amount + 1 } : item));
        let updataTurnAndPrice = updataTurnDB.map((item) =>
            item.productId === id
                ? { ...item, priceT: item.amount * parseInt(item.price.replace(/\./g, ''), 10) }
                : item,
        );
        setData(updataTurnAndPrice);
    };

    // x l size db
    const handleSizeDB = (id, e) => {
        let updateSize = data.map((item) => (item.productId === id ? { ...item, size: e } : item));
        setData(updateSize);
        setSize(e);
    };

    // x l delete sn phm trong gi hng DB
    const handleDeleteDB = async (id) => {
        try {
            let res = await restfulApi.deleteShoppingCart(id);
            if (res && res.data && res.data.EC === 0) {
                toast.success(res.data.EM);
                setTimeout(() => {
                    navigator(0);
                }, 5000);
            }
        } catch (error) {
            console.log(error);
        }
    };

    // x l tnh ton sn phm
    useEffect(() => {
        const handleCountProduct = () => {
            let countProduct = Object.keys(selectedProducts).filter((item) => selectedProducts[item] === true);
            let count = countProduct.length;
            let totalPrice = countProduct.reduce((sum, id) => {
                let result = data.find((item) => item.id == id);
                if (result) {
                    setDataPayment((prev) => {
                        // Thm sn phm vo dataPayment nu n cha tn ti
                        if (!prev.some((product) => product.id === result.id)) {
                            return [...prev, result];
                        }
                        return prev; // Nu  tn ti, gi nguyn danh sch
                    });
                    return sum + parseInt(result.price.replace(/\./g, ''), 10) * result.amount;
                }
                return sum;
            }, 0);
            // Xa cc sn phm khng c chn (false)
            setDataPayment((prev) => prev.filter((item) => selectedProducts[item.id]));
            setTotalPriceDB(totalPrice);
            setCountProductDB(count);
        };
        handleCountProduct();
    }, [selectedProducts, data]);

    /////////////////////////////////////////////////////////

    // code localStory
    // x l on click out size
    const handleHide = (id) => {
        setShowClassification((prev) => ({ ...prev, [id]: false }));
    };

    // x l onClickoutsize delete
    const handleHideDelete = (id) => {
        setShowAction((prev) => ({ ...prev, [id]: false }));
    };
    // take data shopping car in localStory
    useEffect(() => {
        // Hm ly d liu gi hng t localStorage
        const getDataFromLocalStorage = () => {
            let takeDataShoppingCartInLocal = JSON.parse(localStorage.getItem('info_cart')) || [];
            setDataShoppingCartLocal(takeDataShoppingCartInLocal);
        };
        // Ly d liu gi hngkhi component mount
        getDataFromLocalStorage();
    }, []);
    // Hm x l khi thay i trng thi checkbox ca tng sn phm
    const handleCheckboxChangeLocal = (id) => {
        setSelectedProductsLocal((prevSelectedProducts) => ({
            ...prevSelectedProducts,
            [id]: !prevSelectedProducts[id], // o trng thi checkbox
        }));
    };
    // x l turn in LocalStory
    const handleTurn = (id, e) => {
        const display = dataShoppingCartLocal.map((item) => (item.productId === id ? { ...item, amount: e } : item));
        setDataShoppingCartLocal(display);
    };
    const handlePrev = (id) => {
        const updataLocal = dataShoppingCartLocal.map((item) =>
            item.productId === id && item.amount > 1 ? { ...item, amount: item.amount - 1 } : item,
        );
        const updataNew = updataLocal.map((item) =>
            item.productId === id
                ? { ...item, priceT: parseInt(item.price.replace(/\./g, ''), 10) * item.amount }
                : item,
        );
        setDataShoppingCartLocal(updataNew);
    };
    const handleNext = (id) => {
        const updataLocal = dataShoppingCartLocal.map((item) =>
            item.productId === id ? { ...item, amount: item.amount + 1 } : item,
        );
        const updataNew = updataLocal.map((item) =>
            item.productId === id
                ? { ...item, priceT: parseInt(item.price.replace(/\./g, ''), 10) * item.amount }
                : item,
        );

        setDataShoppingCartLocal(updataNew);
    };

    // x l size
    const handleSize = (id, newSize) => {
        const updata = dataShoppingCartLocal.map((item) => (item.productId === id ? { ...item, size: newSize } : item));
        setDataShoppingCartLocal(updata);
    };

    // x l xa shopping cart
    const handleDeleteLocal = (id) => {
        let cart = JSON.parse(localStorage.getItem('info_cart')); // Ly v chuyn i d liu t localStorage
        if (cart) {
            // Lc b sn phm c productId bng vi id
            let updatedCart = cart.filter((item) => item.productId !== id);
            // Cp nht li localStorage vi mng  lc
            localStorage.setItem('info_cart', JSON.stringify(updatedCart));
            // Cp nht li state vi d liu mi
            setDataShoppingCartLocal(updatedCart);
        }
    };

    // x l m sn phm  check
    // lu :
    // selectedProductsLocal l mt object, nhng bn ang dng .map()
    //  x l nh mt mng. Thay vo , bn cn dng
    // Object.keys()  ly danh sch cc id sn phm  c chn.
    //
    // Object.keys(selectedProductsLocal) tr v danh sch cc id ca sn phm.
    // filter() kim tra xem sn phm no c gi tr true ( c check).
    useEffect(() => {
        const handleCountProduct = () => {
            let checkProduct = Object.keys(selectedProductsLocal).filter((id) => selectedProductsLocal[id] === true);
            let count = checkProduct.length;

            // Tnh tng gi tin cc sn phm  c chn
            let totalPrice = checkProduct.reduce((sum, selectedId) => {
                let product = dataShoppingCartLocal.find((item) => item.productId === selectedId); // Tm sn phm theo ID
                if (product) {
                    console.log('product found', product);
                    return sum + parseInt(product.price.replace(/\./g, ''), 10) * product.amount; // Cng gi vo tng
                }
                return sum; // Nu khng tm thy sn phm, tr v sum hin ti
            }, 0);

            setCountProduct(count);
            setCountPrice(totalPrice);
        };

        handleCountProduct();
    }, [selectedProductsLocal, dataShoppingCartLocal]);

    // x l chn m gim gi
    const handleChooseVoucher = () => {
        setShowVoucher((prev) => !prev);
    };

    // x l mua hng
    const handlePurchase = async () => {
        try {
            if (checkLogin === 'true') {
                setShowPayCK((prev) => !prev);
                let res = await restfulApi.payment({
                    listProduct: dataPayment,
                    totalPrice: totalPriceDB,
                });
                if (res && res.data && res.data.EC === 0) {
                    setPayUrl(res.data.DT);
                }
            } else {
                toast.warning('Vui lng ng nhp trc khi mua hng');
                setTimeout(() => {
                    navigator('/login');
                }, 6000);
            }
        } catch (error) {
            console.log(error);
        }
    };
    const handlePayClose = () => {
        setShowPayCK(false);
    };
    const handlePayNext = () => {
        window.location.href = payUrl.payUrl;
    };
    return (
        <>
            <div className={cx('shopping-cart')}>
                {/*  */}
                {checkLogin === 'true' ? (
                    <>
                        <div className={cx('header-cart')}>
                            <div className="row">
                                <div className={cx('col-lg-6', 'left')}>
                                    <div className={cx('tick')}>
                                        <input
                                            type="checkbox"
                                            onClick={() => {
                                                // Hm every trong JavaScript kim tra xem tt c cc phn t trong mng c tha mn mt iu kin c th hay khng.
                                                // Nu tt c cc phn t trong mng tha mn iu kin, hm s tr v true;
                                                // nu ch cn mt phn t khng tha mn, n s tr v false
                                                const allSelected = data.every((item) => selectedProducts[item.id]);
                                                // Hm reduce trong JavaScript cho php bn gim (reduce) mt mng xung thnh mt gi tr duy nht,
                                                // bng cch thc thi mt hm ln mi phn t ca mng (t tri sang phi) v gi li kt qu trong mt bin tch ly.
                                                const updatedSelected = data.reduce((acc, item) => {
                                                    acc[item.id] = !allSelected;
                                                    return acc;
                                                }, {});
                                                setSelectedProducts(updatedSelected);
                                            }}
                                            checked={data.every((item) => selectedProducts[item.id])} // Kim tra tt c checkbox  c chn hay cha/>
                                        />
                                        <span>Sn phm </span>
                                    </div>
                                </div>
                                <div className={cx('col-lg-6', 'right')}>
                                    <ul>
                                        <li>n gi</li>
                                        <li>S lng</li>
                                        <li>S tin</li>
                                        <li>Thao tc</li>
                                    </ul>
                                </div>
                            </div>
                        </div>
                        {data &&
                            data.length > 0 &&
                            data.map((item, index) => {
                                return (
                                    <div className={cx('info-product')} key={index}>
                                        <div className="row">
                                            <div className={cx('col-lg-6', 'left')}>
                                                <div className={cx('info-wrapper')}>
                                                    <input
                                                        type="checkbox"
                                                        checked={selectedProducts[item.id] || false}
                                                        onChange={() => handleCheckboxChange(item.id)} // S kin thay i trng thi checkbox
                                                    />
                                                    <div className={cx('product')}>
                                                        <Images
                                                            src={`http://localhost:8081/images/${item.images}`}
                                                            alt="error"
                                                            className={cx('img')}
                                                        />
                                                        <div className={cx('tit')}>
                                                            <span>{item.title}</span>
                                                            <p>i tr trong 17 ngy </p>
                                                        </div>
                                                    </div>
                                                    <div className={cx('classification')}>
                                                        <Tippy
                                                            visible={showClassification[item.id] || false}
                                                            interactive={true}
                                                            onClickOutside={() => handleClassification(item.id)}
                                                            placement="bottom"
                                                            theme={'light'}
                                                            content={
                                                                <div className={cx('wrapper')}>
                                                                    <span>Th loi:</span>
                                                                    <div className={cx('catagory')}>
                                                                        <button
                                                                            className={cx(
                                                                                'item',
                                                                                `${item.type === 'o' ? 'act' : ''}`,
                                                                            )}
                                                                        >
                                                                            o
                                                                        </button>
                                                                        <button
                                                                            className={cx(
                                                                                'item',
                                                                                `${item.type === 'qun' ? 'act' : ''}`,
                                                                            )}
                                                                        >
                                                                            Qun
                                                                        </button>
                                                                        <button
                                                                            className={cx(
                                                                                'item',
                                                                                `${item.type === 'giy' ? 'act' : ''}`,
                                                                            )}
                                                                        >
                                                                            Giy
                                                                        </button>
                                                                        <button
                                                                            className={cx(
                                                                                'item',
                                                                                `${
                                                                                    item.type === 'ph kin'
                                                                                        ? 'act'
                                                                                        : ''
                                                                                }`,
                                                                            )}
                                                                        >
                                                                            Ph kin
                                                                        </button>
                                                                    </div>
                                                                    <span>Size:</span>
                                                                    <div className={cx('size')}>
                                                                        {item.size === 'XX' ||
                                                                        item.size === 'LG' ||
                                                                        item.size === 'LN' ? (
                                                                            <>
                                                                                <button
                                                                                    className={cx(
                                                                                        'item',
                                                                                        `${
                                                                                            item.size === 'LG'
                                                                                                ? 'act'
                                                                                                : ''
                                                                                        }`,
                                                                                    )}
                                                                                    onClick={() =>
                                                                                        handleSizeDB(
                                                                                            item.productId,
                                                                                            'LG',
                                                                                        )
                                                                                    }
                                                                                >
                                                                                    LG
                                                                                </button>
                                                                                <button
                                                                                    className={cx(
                                                                                        'item',
                                                                                        `${
                                                                                            item.size === 'XX'
                                                                                                ? 'act'
                                                                                                : ''
                                                                                        }`,
                                                                                    )}
                                                                                    onClick={() =>
                                                                                        handleSizeDB(
                                                                                            item.productId,
                                                                                            'XX',
                                                                                        )
                                                                                    }
                                                                                >
                                                                                    XX
                                                                                </button>
                                                                                <button
                                                                                    className={cx(
                                                                                        'item',
                                                                                        `${
                                                                                            item.size === 'XL'
                                                                                                ? 'act'
                                                                                                : ''
                                                                                        }`,
                                                                                    )}
                                                                                    onClick={() =>
                                                                                        handleSizeDB(
                                                                                            item.productId,
                                                                                            'XL',
                                                                                        )
                                                                                    }
                                                                                >
                                                                                    XL
                                                                                </button>
                                                                            </>
                                                                        ) : (
                                                                            <select
                                                                                className={cx('size')}
                                                                                id="fruit"
                                                                                name="fruit"
                                                                                value={size}
                                                                                //onClick={() => handleSileBtn(size)}
                                                                                onChange={(e) =>
                                                                                    handleSizeDB(
                                                                                        item.productId,
                                                                                        e.target.value,
                                                                                    )
                                                                                }
                                                                            >
                                                                                <option selected>size</option>
                                                                                <option value="39">39</option>
                                                                                <option value="40">40</option>
                                                                                <option value="41">41</option>
                                                                                <option value="42">42</option>
                                                                            </select>
                                                                        )}
                                                                    </div>
                                                                </div>
                                                            }
                                                        >
                                                            <span onClick={() => handleClassification(item.id)}>
                                                                {' '}
                                                                Phn loi{' '}
                                                                <i
                                                                    className={`fa-solid fa-caret-${
                                                                        showClassification[item.id] ? 'up' : 'down'
                                                                    }`}
                                                                ></i>
                                                            </span>
                                                        </Tippy>
                                                    </div>
                                                </div>
                                            </div>
                                            <div className={cx('col-lg-6', 'right')}>
                                                <ul>
                                                    <li>
                                                        <span>{item.price}</span>
                                                    </li>
                                                    <li>
                                                        <div>
                                                            <button onClick={() => handleTurnPrevDB(item.productId)}>
                                                                -
                                                            </button>
                                                            <input type="text" value={item.amount} />
                                                            <button onClick={() => handleTurnNextDB(item.productId)}>
                                                                +
                                                            </button>
                                                        </div>
                                                    </li>
                                                    <li>
                                                        <ins>{(item.priceT || item.price).toLocaleString('vn')}</ins>
                                                    </li>
                                                    <li>
                                                        <Tippy
                                                            visible={showAction[item.id] || false}
                                                            interactive={true}
                                                            onClickOutside={() => handleAction(item.id)}
                                                            placement="bottom"
                                                            theme={'light'}
                                                            content={
                                                                <div className={cx('action')}>
                                                                    <button onClick={() => handleDeleteDB(item.id)}>
                                                                        Xa
                                                                    </button>
                                                                </div>
                                                            }
                                                        >
                                                            <span
                                                                className={cx('ac')}
                                                                onClick={() => handleAction(item.id)}
                                                            >
                                                                thao tc{' '}
                                                                <i
                                                                    className={`fa-solid fa-caret-${
                                                                        showAction[item.id] ? 'up' : 'down'
                                                                    }`}
                                                                ></i>
                                                            </span>
                                                        </Tippy>
                                                    </li>
                                                </ul>
                                            </div>
                                        </div>
                                    </div>
                                );
                            })}
                    </>
                ) : (
                    <>
                        <div className={cx('header-cart')}>
                            <div className="row">
                                <div className={cx('col-lg-6', 'left')}>
                                    <div className={cx('tick')}>
                                        <input
                                            type="checkbox"
                                            onClick={() => {
                                                // Hm every trong JavaScript kim tra xem tt c cc phn t trong mng c tha mn mt iu kin c th hay khng.
                                                // Nu tt c cc phn t trong mng tha mn iu kin, hm s tr v true;
                                                // nu ch cn mt phn t khng tha mn, n s tr v false
                                                const allSelected = dataShoppingCartLocal.every(
                                                    (item) => selectedProductsLocal[item.productId],
                                                );
                                                // Hm reduce trong JavaScript cho php bn gim (reduce) mt mng xung thnh mt gi tr duy nht,
                                                // bng cch thc thi mt hm ln mi phn t ca mng (t tri sang phi) v gi li kt qu trong mt bin tch ly.
                                                const updatedSelected = dataShoppingCartLocal.reduce((acc, item) => {
                                                    acc[item.productId] = !allSelected;
                                                    return acc;
                                                }, {});
                                                setSelectedProductsLocal(updatedSelected);
                                            }}
                                            checked={dataShoppingCartLocal.every(
                                                (item) => selectedProductsLocal[item.productId],
                                            )} // Kim tra tt c checkbox  c chn hay cha/>
                                        />
                                        <span>Sn phm </span>
                                    </div>
                                </div>
                                <div className={cx('col-lg-6', 'right')}>
                                    <ul>
                                        <li>n gi</li>
                                        <li>S lng</li>
                                        <li>S tin</li>
                                        <li>Thao tc</li>
                                    </ul>
                                </div>
                            </div>
                        </div>
                        {dataShoppingCartLocal &&
                            dataShoppingCartLocal.length > 0 &&
                            dataShoppingCartLocal.map((item, index) => {
                                return (
                                    <div className={cx('info-product')} key={index}>
                                        <div className="row">
                                            <div className={cx('col-lg-6', 'left')}>
                                                <div className={cx('info-wrapper')}>
                                                    <input
                                                        type="checkbox"
                                                        checked={selectedProductsLocal[item.productId] || false}
                                                        onChange={() => handleCheckboxChangeLocal(item.productId)} // S kin thay i trng thi checkbox
                                                    />
                                                    <div className={cx('product')}>
                                                        <Images
                                                            src={`http://localhost:8081/images/${item.image}`}
                                                            alt="error"
                                                            className={cx('img')}
                                                        />
                                                        <div className={cx('tit')}>
                                                            <span>{item.title}</span>
                                                            <p>i tr trong 17 ngy </p>
                                                        </div>
                                                    </div>
                                                    <div className={cx('classification')}>
                                                        <Tippy
                                                            visible={showClassification[item.productId] || false}
                                                            onClickOutside={() => handleHide(item.productId)}
                                                            interactive={true}
                                                            placement="bottom"
                                                            theme={'light'}
                                                            content={
                                                                <div className={cx('wrapper')}>
                                                                    <span>Th loi:</span>
                                                                    <div className={cx('catagory')}>
                                                                        <button
                                                                            className={cx(
                                                                                'item',
                                                                                `${item.type === 'o' ? 'act' : ''}`,
                                                                            )}
                                                                        >
                                                                            o
                                                                        </button>
                                                                        <button
                                                                            className={cx(
                                                                                'item',
                                                                                `${item.type === 'qun' ? 'act' : ''}`,
                                                                            )}
                                                                        >
                                                                            Qun
                                                                        </button>
                                                                        <button
                                                                            className={cx(
                                                                                'item',
                                                                                `${item.type === 'giy' ? 'act' : ''}`,
                                                                            )}
                                                                        >
                                                                            Giy
                                                                        </button>
                                                                        <button
                                                                            className={cx(
                                                                                'item',
                                                                                `${
                                                                                    item.type === 'ph kin'
                                                                                        ? 'act'
                                                                                        : ''
                                                                                }`,
                                                                            )}
                                                                        >
                                                                            Ph kin
                                                                        </button>
                                                                    </div>
                                                                    <span>Size:</span>
                                                                    <div className={cx('size')}>
                                                                        <button
                                                                            className={cx(
                                                                                'item',
                                                                                `${item.size === 'LG' ? 'act' : ''}`,
                                                                            )}
                                                                            onClick={() =>
                                                                                handleSize(item.productId, 'LG')
                                                                            }
                                                                        >
                                                                            LG
                                                                        </button>
                                                                        <button
                                                                            className={cx(
                                                                                'item',
                                                                                `${item.size === 'XX' ? 'act' : ''}`,
                                                                            )}
                                                                            onClick={() =>
                                                                                handleSize(item.productId, 'XX')
                                                                            }
                                                                        >
                                                                            XX
                                                                        </button>
                                                                        <button
                                                                            className={cx(
                                                                                'item',
                                                                                `${item.size === 'XL' ? 'act' : ''}`,
                                                                            )}
                                                                            onClick={() =>
                                                                                handleSize(item.productId, 'XL')
                                                                            }
                                                                        >
                                                                            XL
                                                                        </button>
                                                                    </div>
                                                                </div>
                                                            }
                                                        >
                                                            <span onClick={() => handleClassification(item.productId)}>
                                                                {' '}
                                                                Phn loi{' '}
                                                                <i
                                                                    className={`fa-solid fa-caret-${
                                                                        showClassification[item.productId]
                                                                            ? 'up'
                                                                            : 'down'
                                                                    }`}
                                                                ></i>
                                                            </span>
                                                        </Tippy>
                                                    </div>
                                                </div>
                                            </div>
                                            <div className={cx('col-lg-6', 'right')}>
                                                <ul>
                                                    <li>
                                                        <span>{item.price}</span>
                                                    </li>
                                                    <li>
                                                        <div>
                                                            <button onClick={() => handlePrev(item.productId)}>
                                                                -
                                                            </button>
                                                            <input
                                                                type="text"
                                                                value={item.amount}
                                                                onChange={(e) =>
                                                                    handleTurn(Number(item.productId, e.target.value))
                                                                }
                                                            />
                                                            <button onClick={() => handleNext(item.productId)}>
                                                                +
                                                            </button>
                                                        </div>
                                                    </li>
                                                    <li>
                                                        <ins>{(item.priceT || item.price).toLocaleString('vi-VN')}</ins>
                                                    </li>
                                                    <li>
                                                        <Tippy
                                                            visible={showAction[item.productId] || false}
                                                            onClickOutside={() => handleHideDelete(item.productId)}
                                                            interactive={true}
                                                            placement="bottom"
                                                            theme={'light'}
                                                            content={
                                                                <div className={cx('action')}>
                                                                    <button
                                                                        onClick={() =>
                                                                            handleDeleteLocal(item.productId)
                                                                        }
                                                                    >
                                                                        Xa
                                                                    </button>
                                                                </div>
                                                            }
                                                        >
                                                            <span
                                                                className={cx('ac')}
                                                                onClick={() => handleAction(item.productId)}
                                                            >
                                                                thao tc{' '}
                                                                <i
                                                                    className={`fa-solid fa-caret-${
                                                                        showAction[item.id] ? 'up' : 'down'
                                                                    }`}
                                                                ></i>
                                                            </span>
                                                        </Tippy>
                                                    </li>
                                                </ul>
                                            </div>
                                        </div>
                                    </div>
                                );
                            })}
                    </>
                )}
                {/* thanh ton */}
                <div className={cx('info_pay')}>
                    <div className={cx('row')}>
                        <div className={cx('col-lg-12', 'center')}>
                            <div className={cx('info_voucher')}>
                                <div className={cx('voucher')}>
                                    <i class="fa-sharp fa-light fa-ticket"></i>
                                    <span>Chn Voucher</span>
                                </div>
                                <button className={cx('add_voucher')} onClick={handleChooseVoucher}>
                                    Chon hoc nhp m voucher
                                </button>
                            </div>
                            <div className={cx('pay')}>
                                <span className={cx('tit')}>{`Tng thanh ton (${
                                    checkLogin === 'true' ? countProductDB : countProduct
                                } sn phm)`}</span>
                                <div className={cx('price')}>
                                    <div className={cx('total_price')}>
                                        <ins className={cx('pr')}>
                                            {(checkLogin === 'true' ? totalPriceDB : countPrice).toLocaleString(
                                                'vi-VN',
                                            )}
                                            
                                        </ins>
                                        <Tippy
                                            interactive
                                            placement="top"
                                            theme={'light'}
                                            content={
                                                <div className={cx('info')}>
                                                    <h4 style={{ fontSize: '17px' }}>Chi tit khuyn mi</h4>
                                                    <ul>
                                                        <li
                                                            style={{
                                                                display: 'flex',
                                                                alignItems: 'center',
                                                                justifyContent: 'space-between',
                                                            }}
                                                        >
                                                            <span>Tng tin hng</span>
                                                            <ins style={{ color: '#ff4500' }}>
                                                                {(checkLogin === 'true'
                                                                    ? totalPriceDB
                                                                    : countPrice
                                                                ).toLocaleString('vi-VN')}
                                                                
                                                            </ins>
                                                        </li>
                                                        <li
                                                            style={{
                                                                display: 'flex',
                                                                alignItems: 'center',
                                                                justifyContent: 'space-between',
                                                            }}
                                                        >
                                                            <span>Voucher gim gi</span>
                                                            <ins style={{ color: '#ff4500' }}>0</ins>
                                                        </li>
                                                        <li
                                                            style={{
                                                                display: 'flex',
                                                                alignItems: 'center',
                                                                justifyContent: 'space-between',
                                                            }}
                                                        >
                                                            <span>Tng s tin</span>
                                                            <ins style={{ color: '#ff4500' }}>
                                                                {(checkLogin === 'true'
                                                                    ? totalPriceDB
                                                                    : countPrice
                                                                ).toLocaleString('vi-VN')}
                                                                
                                                            </ins>
                                                        </li>
                                                        <li>
                                                            <p>S tin cui cng phi thanh ton</p>
                                                        </li>
                                                    </ul>
                                                </div>
                                            }
                                        >
                                            <i class="fa-solid fa-caret-up"></i>
                                        </Tippy>
                                    </div>
                                    <p>Tit kim: </p>
                                </div>
                            </div>
                            <button className={cx('btn-buy')} onClick={() => handlePurchase()}>
                                Mua hng
                            </button>
                        </div>
                    </div>
                </div>
                {/* end */}
            </div>
            {/* x l chn m gim gi */}
            <div className={cx('choose_voucher', `${!showVoucher ? 'hide_voucher' : ''}`)}>
                <div className={cx('wrapper_voucher')}>
                    <div className={cx('enter_code')}>
                        <input placeholder="Nhp m gim gi nu c" />
                    </div>
                    <div className={cx('choose')}></div>
                    <div className={cx('action')}>
                        <button className={cx('prev')} onClick={handleChooseVoucher}>
                            Tr li
                        </button>
                        <button className={cx('ok')}>Chn</button>
                    </div>
                </div>
            </div>
            <div className={cx('background', `${!showVoucher ? 'hide_voucher' : ''}`)}></div>
            {/* xc thc thanh ton */}
            <div className={cx('agree_to_pay', `${!showPayCK ? 'pay_hide' : ''}`)}>
                <div className={cx('wrapper')}>
                    <button className={cx('close')} onClick={handlePayClose}>
                        Close
                    </button>
                    <button className={cx('next')} onClick={handlePayNext}>
                        Tip tc
                    </button>
                </div>
            </div>
            {/*  */}
            <ToastContainer
                position="top-right"
                autoClose={5000}
                hideProgressBar={false}
                newestOnTop={false}
                closeOnClick
                rtl={false}
                pauseOnFocusLoss
                draggable
                pauseOnHover
                theme="light"
            />
        </>
    );
}

export default ShoppingCart;
